/*  */

h1 {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
}

.column {
    display: inline-block;
    text-align: center;
}

figure {
    overflow: hidden;
}

a p {
    color: black;
    margin-top: 8px;
    font-family: 'Open Sans', sans-serif;
}

a:hover {
    text-decoration: none;
}

.column img {
    display: block !important;
    width: 160px !important;
    height: 160px !important;
    border-radius: 100% !important;
    border: solid 7px #321124 !important;
}


/* CSS Image Hover. Created refering to Naoya's Pen: https://codepen.io/nxworld/pen/ZYNOBZ */


/*Sepia*/

.column#sepia img {
    -webkit-filter: sepia(100%);
    filter: sepia(100%);
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
}

.column#sepia:hover img {
    -webkit-filter: sepia(0);
    filter: sepia(0);
}


/*GrayScale*/

.column#grayscale img {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
}

.column#grayscale:hover img {
    -webkit-filter: grayscale(0);
    filter: grayscale(0);
}


/*With Simple Caption*/

.column#caption {
    position: relative;
}

.column#caption .text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    opacity: 0;
    transition: all 0.8s ease;
}

.column#caption .text h1 {
    margin: 0;
    color: white;
}

.column#caption:hover .text {
    opacity: 1;
}

.column#caption:hover img {
    -webkit-filter: sepia(90%);
}


/* Craeted refering to LittleSnippets.net Pen: https://codepen.io/littlesnippets/pen/adLELd */

.frame {
    text-align: center;
    position: relative;
    cursor: pointer;
    perspective: 500px;
}

.frame img {
    width: 300px;
    height: 300px;
}

.frame .details {
    width: 70%;
    height: 80%;
    padding: 5% 8%;
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotateY(90deg);
    transform-origin: 50%;
    background: rgba(255, 255, 255, 0.9);
    opacity: 0;
    transition: all 0.4s ease-in;
}

.frame:hover .details {
    transform: translate(-50%, -50%) rotateY(0deg);
    opacity: 1;
}

